  .envelope {
    position: relative;
    display: block;
    width: 30em;
    height: 35em;
    margin: 0 auto;
  }
  .envelope.active .content {
    padding: 15em 2em 2em;
  }
  .envelope.active .paper.front,
  .envelope.active .paper.back {
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
  .envelope.active .paper.front {
    -webkit-animation-name: envelope-front;
            animation-name: envelope-front;
  }
  .envelope.active .paper.back {
    -webkit-animation-name: envelope-back;
            animation-name: envelope-back;
  }
  .envelope.active .paper.back:before {
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 1.25s;
            animation-delay: 1.25s;
    -webkit-animation-name: envelope-back-before;
            animation-name: envelope-back-before;
  }
  .envelope.active .bottom-wrapper {
    transform: rotateX(180deg);
  }
  .envelope.active .bottom-wrapper:after {
    z-index: 0;
    opacity: 1;
  }
  .envelope .content {
    padding: 2em;
    box-sizing: border-box;
    position: relative;
    z-index: 9;
    transition: all 0.5s ease-in-out;
    transition-delay: 1s;
  }
  .envelope .top-wrapper,
  .envelope .bottom-wrapper {
    box-sizing: border-box;
    background: #82204A;
    color: #fff;
  }
  .envelope .top-wrapper {
    padding: 2em 2em 0;
    border-top-left-radius: 0.5em;
    border-top-right-radius: 0.5em;
  }
  .envelope .bottom-wrapper {
    padding: 0 2em 2em;
    border-bottom-left-radius: 0.5em;
    border-bottom-right-radius: 0.5em;
    transition: all 0.5s ease-in-out;
    transform-origin: top;
    transform-style: preserve-3d;
    position: relative;
    overflow: hidden;
    margin-top: -1px;
  }
  .envelope .bottom-wrapper:after {
    position: absolute;
    content: '';
    display: block;
    opacity: 0;
    background: #82204A;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .envelope form label {
    display: block;
    padding-bottom: 0.5em;
  }
  .envelope form input,
  .envelope form textarea {
    width: 100%;
    box-shadow: 0;
    background: transparent;
    color: #fff;
  }
  .envelope form input {
    border-width: 0 0 0.1em;
    border-color: #fff;
    border-style: solid;
  }
  .envelope form textarea {
    border: 0.1em solid #fff;
    border-radius: 0.25em;
  }
  .envelope form .submit-card {
    background: #fff;
    color: #222;
    text-align: center;
    padding: 0.5em;
    box-sizing: border-box;
    background: #fff;
    width: 100%;
    border: 0;
    box-shadow: none;
    border-radius: 0.25em;
    cursor: pointer;
  }
  .envelope form .input {
    padding-bottom: 1em;
  }
  .envelope .paper {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    border-bottom-left-radius: 0.5em;
    border-bottom-right-radius: 0.5em;
    overflow: hidden;
  }
  .envelope .paper.back {
    top: 0;
  }
  .envelope .paper.back:before {
    content: '';
    display: block;
    width: 0;
    height: 0;
    margin-bottom: -1px;
    border-style: solid;
    border-width: 0 15em 10em 15em;
    border-color: transparent transparent #d3d3d3 transparent;
    transform-origin: bottom;
    transform-style: preserve-3d;
    z-index: 0;
  }
  .envelope .paper.back:after {
    content: '';
    display: block;
    background-color: #d3d3d3;
    width: 30em;
    height: 20em;
  }
  .envelope .paper.front {
    top: 10em;
    box-shadow: 0.1em 0.5em 0.5em rgba(0,0,0,0.25);
    z-index: 0;
  }
  .envelope .paper.front:before {
    content: '';
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10em 15em 0 15em;
    border-color: transparent #fff;
  }
  .envelope .paper.front:after {
    content: '';
    display: block;
    width: 30em;
    height: 10em;
    background: #fff;
    margin-top: -1px;
  }
  @-webkit-keyframes envelope-front {
    0% {
      top: 10em;
      z-index: 0;
    }
    50% {
      top: 15em;
      z-index: 9;
    }
    100% {
      top: 10em;
      z-index: 9;
    }
  }
  @keyframes envelope-front {
    0% {
      top: 10em;
      z-index: 0;
    }
    50% {
      top: 15em;
      z-index: 9;
    }
    100% {
      top: 10em;
      z-index: 9;
    }
  }
  @-webkit-keyframes envelope-back {
    0% {
      top: 0;
    }
    50% {
      top: 5em;
    }
    100% {
      top: 0;
    }
  }
  @keyframes envelope-back {
    0% {
      top: 0;
    }
    50% {
      top: 5em;
    }
    100% {
      top: 0;
    }
  }
  @-webkit-keyframes envelope-back-before {
    0% {
      border-color: transparent transparent #d3d3d3 transparent;
      transform: rotateX(0deg);
      z-index: 0;
    }
    100% {
      border-color: transparent transparent #fff transparent;
      transform: rotateX(180deg);
      z-index: 99;
      position: relative;
    }
  }
  @keyframes envelope-back-before {
    0% {
      border-color: transparent transparent #d3d3d3 transparent;
      transform: rotateX(0deg);
      z-index: 0;
    }
    100% {
      border-color: transparent transparent #fff transparent;
      transform: rotateX(180deg);
      z-index: 99;
      position: relative;
    }
  }
  
*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  }


body:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    height: 100%;
}

h1 {
  display: inline-block;
  color: #353b3c;
  font-family: 'Righteous', serif;
  font-size: 5em; 
  text-shadow: .03em .03em 0 #f0a7a0;
}

h1:after {
    content: attr(data-shadow);
    position: absolute;
    top: .09em; left: .06em;
    z-index: -1;
    text-shadow: none;
    background-image:
      linear-gradient(
        45deg,
        transparent 45%,
        #331832 45%,
       #F0544F 55%,
        transparent 0
        );
    background-size: .05em .05em;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-animation: shad-anim 15s linear infinite;
            animation: shad-anim 15s linear infinite;
}

@-webkit-keyframes shad-anim {
  0% {background-position: 0 0}
  0% {background-position: 100% -100%}
  }

@keyframes shad-anim {
  0% {background-position: 0 0}
  0% {background-position: 100% -100%}
  }
